import React, { useRef, useEffect} from 'react';
import './Account.scss';
import { Outlet, useLocation, useNavigate } from 'react-router-dom';
import { ACCOUNT_PROFILE_ROUTE, ACCOUNT_SECURITY_ROUTE, ACCOUNT_NOTIFICATIONS_ROUTE } from '../../utils/consts';
import SideMenu from './../../components/SideMenu/SideMenu';
import { ACCOUNT_ROUTE, OWERVIEW_ROUTE, UI_MOBILE } from './../../utils/consts';
import LinkBack from './../../components/LinkBack/LinkBack';
import { logout } from './../../API/userAPI';
import userStore from '../../store/UserStore';
import UI from '../../store/UI';
import { observer } from 'mobx-react-lite';

const Account = observer(() => {
  const { pathname } = useLocation();
  const sideMenu = useRef(null);
  const navigate = useNavigate();

  useEffect(() => {
    if(UI.device === UI_MOBILE) {
      if(pathname === ACCOUNT_ROUTE) {
        sideMenu?.current.classList.remove('hidden');
        sideMenu?.current.classList.add('visible');
      }
      else {
        sideMenu?.current.classList.remove('visible');
        sideMenu?.current.classList.add('hidden');
      }
    } else {
      sideMenu?.current.classList.remove('hidden');
      sideMenu?.current.classList.add('visible');
      if(pathname === ACCOUNT_ROUTE) {
        navigate(ACCOUNT_PROFILE_ROUTE);
      }
    }
  }, [pathname, UI.device]);

  return (
    <div className='account'>
      <div className='account__menu' ref={sideMenu}>
        <LinkBack path={OWERVIEW_ROUTE} title='Account setting' />
        <SideMenu 
          title='Account setting' 
          paths={[
            {pathName: 'Account', pathLink: ACCOUNT_PROFILE_ROUTE},
            {pathName: 'Security', pathLink: ACCOUNT_SECURITY_ROUTE},
            {pathName: 'Notification setting', pathLink: ACCOUNT_NOTIFICATIONS_ROUTE},
            {pathName: 'Log out', pathLink: ACCOUNT_ROUTE, className: 'account__logout', onClick: async () => {
              await logout();
              userStore.setCurrentUser(null);
              localStorage.removeItem('user');
              window.location.reload(); // to reset store
            }}
          ]}
        />    
      </div>
      <div className='account__page'>
        <Outlet />
      </div>
    </div>
  );
});

export default Account;